<template>
  <div class="login-page">
    <b-form class="form-signin">
      <b-img 
        class="mb-4"
        src="https://getbootstrap.com/docs/4.0/assets/brand/bootstrap-solid.svg" alt="" 
        width="72"
        height="72"/>
      <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>

        <b-form-input
          v-model="loginForm.email"
          type="email"
          placeholder="Email Address"
          required
        ></b-form-input>
        <b-form-input
          v-model="loginForm.password"
          type="password"
          placeholder="Password"
          required
        ></b-form-input>
      <b-button
        block
        size="lg" 
        variant="primary" 
        @click="handleLogin">Sign in
      </b-button>
      <p class="mt-2 mb-3 text-muted">© 2022 WareHouse Booking System</p>
    </b-form>
  </div>
</template>
<style scoped>
  .form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
    margin-top: 100px;
  }

  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  .form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
</style>
<script>
// import { mapGetters, mapState, mapActions } from 'vuex'
export default {
  name: 'LoginView',
  data() {
    return {
      loginForm: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    handleLogin() {
      this.$store.dispatch('auth/login', this.loginForm).then(() => {
        this.$toasted.success('Login Success!')
        this.$router.push('/home/calendar')
      }).catch(() => {

      })
    }
  } 
}
</script>